<template lang="en">
<section>
    <div class="container-fluid p-0">
        <div class="col-md-12 p-0 mt-2">
            <div class="theme-primary partition-full">
                <span class="p-name text-white">{{$t('TrackTime')}}</span>
                <span class="p-actions float-right">
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info" @click="$emit('addManualTime')"><i class="fa fa-clock-o mx-2"></i><span>{{$t('AutomaticEntry')}} </span></a>
                </span>
            </div>
        </div>
        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
            <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                <div class="border bg-light p-3 newtimetrack mt-2 float-left w-100">
                    <div class="basebod bg-white float-left w-100">
                        <div class="col-sm-12 col-md-12 col-lg-8 col-xl-8 float-left ">
                            <div class="row">

                                <multiselect placeholder="Search Project" class="form-group tt_ddr col border-0 tt_bod-right" @input="taskByProject" v-model="trackerData.projectId" :options="store.getProjectsByUser" label="projectName" track-by="projectId" :show-labels="false" id="exampleFormControlSelect1">
                                    <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                </multiselect>
                                <multiselect @input="isBillableSatus" placeholder="Search Task" class="form-control tt_ddr col border-0 tt_bod-right" id="exampleFormControlSelect2" v-model="trackerData.taskId" :options="store.getTasksFromProject" label="firstAttribute" track-by="value" :show-labels="false">
                                    <template slot="option" slot-scope="props">
                                        <div class="option__desc"><span class="option__title">{{
                                                props.option.firstAttribute }}</span>
                                            <label class="float-right text-light px-2 ml-1 taskcount">{{ props.option.subTasksCount }}</label>
                                            <label class="float-right"> {{ props.option.thirdAttribute }}% </label>
                                        </div>
                                    </template>
                                    <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                </multiselect>

                                <div class="p-0 position-relative">
                                    <a class="tt_p-action-btn tt_bod-right" @click="tagShow = tagShow = !tagShow; breakModalPopup = false" ref="tags">
                                        <i class="tt_icon ico_tag"></i><span class="text-uppercase">{{tagSelect.length > 0 ? tagSelect.length + ' Selected' : 'Tags'  }}</span>
                                    </a>

                                    <a class="tt_p-action-btn tt_bod-right " @click="trackerData.isBillable = !trackerData.isBillable">
                                        <i :class="trackerData.isBillable ? 'tt_icon ico_dollar' : 'tt_icon ico_dollar-o'"></i><span class="text-uppercase">{{ trackerData.isBillable ? 'Paid' :'Unpaid'}}</span>
                                    </a>
                                    <a ref="break" class="tt_p-action-btn tt_bod-right" @click="breakModalOpen({ breakTimer: false, modalClose: true }); tagShow = false" :class="store.getTimer.enableBreak=='yes' ? '' : 'd-none'">
                                        <i class="tt_icon ico_cutlery"></i><span class="text-uppercase">{{$t('BreakDetail')}}</span>
                                    </a>
                                    <div class="hs_tag tagsModals" v-if="tagShow" ref="tagsModals">

                                        <multiselect :placeholder="$t('searchTags')" :multiple="true" class="form-group  border-0 tt_bod-right" :class="store.getTags.length > 0 ? 'tt_ddr_1 col' : ''" v-model="tagSelect" :options="store.getTags" label="tagName" track-by="tagId" :show-labels="false" id="exampleFormControlSelect1">
                                            <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                        </multiselect>
                                        <!-- <div data-v-0f960004="" class="ml-2 mt-1"><a data-v-0f960004="" class="round-icon-small btn-dark theme-primary" @click="openTagsAdd(true)"><i data-v-0f960004="" alt="+" class="fa fa-plus text-white pt-7 font-18"></i></a></div> -->
                                        <div data-v-0f960004="" class="ml-2 squarebtn">
                                            <a data-v-0f960004="" class="round-icon-small btn-info border-radius-0" @click="openTagsAdd(true)"><i data-v-0f960004="" alt="+" class="fa fa-plus text-white pt-7 font-18"></i></a>
                                            <a data-v-0f960004="" class="round-icon-small btn-danger border-radius-0" @click="clearTags"><i data-v-0f960004="" alt="+" class="fa fa-refresh text-white pt-7 font-18"></i></a></div>
                                    </div>
                                    <div class="hs2 breakModal" v-if="breakModalPopup" ref="breakModal">

                                        <BreakModal ref="breakModal" v-if="breakModalPopup" v-on:close="breakModalOpen({ breakTimer: true, modalClose: false })" v-bind:userId="store.getUserInformation.id" v-bind:timerModule="true" v-bind:trackDataPouse="trackerData" v-bind:automaticBreak="false">
                                        </BreakModal>

                                    </div>

                                </div>
                                <div class="tt_bod-right timepickercontrol pt-4 pr-2 pl-3" style="min-width:290px;">

                                    <!-- <TimepickerInpute class="newddlist float-left" style="width:100px;" /> -->
                                    <input type="time" v-model="startTime" class="w-48 mr-2" @input="timerStart" />
                                    <!-- <vue-timepicker v-model="startTime" format="HH:mm" hide-clear-button></vue-timepicker> -->

                                    <input type="time" v-model="endTime" class="w-48" @input="timerStart">
                                    <!--<vue-timepicker v-model="endTime" format="HH:mm" hide-clear-button></vue-timepicker> -->

                                </div>
                                <div class="tt_bod-right px-1" style="max-width:150px;" name="Date">
                                    <div class="mt-3 float-left float-left">
                                        <DatePicker class="d-flex" v-model="date" :popover="false" :masks="dateMask">
                                            <template #default="{ togglePopover, inputValue, inputEvents }">

                                                <div class="input-group-append cursor-pointer float-left mt-1" v-on:click="togglePopover">
                                                    <span class="input-group-text bg-white border-0">
                                                        <em class="fa fa-calendar"></em>
                                                    </span>
                                                </div>
                                                <input :placeholder="dateFormat" class="form-control datepicker_HS float-left pt-2 border-0" :value="inputValue" v-on="inputEvents" />
                                            </template>
                                        </DatePicker>

                                    </div>
                                </div>

                            </div>

                        </div>

                        <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left ">
                            <div class="row">
                                <ValidationProvider :rules="trackerData.projectId? '':'required'" v-slot="{ errors }" class="tt_textarea_span position-relative" name="Description">
                                    <textarea v-model="trackerData.trackDescription" name="Description" class="form-control tt_textarea" :class="errors[0] ? 'is-invalid':''" style="border-color: #BDBCBD; resize:none" v-on:keydown="textCounter" v-on:keyup="textCounter" v-on:mouseout="textCounter" :placeholder="$t('PleaseEnterDescription')" maxlength="2000" @change="descriptionChange" />
                                    <span class="val_msg">{{ errors[0] ? $t('DescRequired') : '' }}</span>
                                </ValidationProvider>

                                <div class="tt_play_btnbase" >
                                    <button class="tt_play_btn tt_bod-right" type="submit">
                                        <i class="fa fa-save text-white"></i>
                                    </button>
                                </div>

                                <div id="TimerCanves" class="DivTimer timerclck" >
                                    {{ timer }}
                                </div>

                            </div>

                        </div>

                    </div>

                </div>

            </form>
        </ValidationObserver>

        </div>
        <!-- <div class="my-popups" v-if="breakModalPopup">
    <div class="modal start-timer cust_timer d-block">
    <div class="modal-dialog modal-dialog-centered" >
        <div class="modal-content" id="DivTimerContent">
            <div class="modal-header">
                <h4 class="modal-title">Choose Break</h4>
                <button type="button" class="close" style="background: none;color: #fff !important;" @click="breakModalOpen({modalClose:false})"><em aria-hidden="true" class="fa fa-times"></em></button>
            </div>
            <div class="modal-body" id="divModalTimetrack">
                <dynamic-form lang="en" :buttons="buttons" ref="form" :schema="FormSchema" v-on:OnSubmit="breakSubmit()">
                   <template v-slot:tgslot-button="{ data }">

                    </template> -->
                <!-- </dynamic-form>
                <div class="start-break col-12 float-left p-0" v-on:click="breakSubmit">
                                    <a class="btn btn-primary text-white btn-lg starttimer w-100" id="btnBreakStart" title="" style="cursor: pointer" data-toggle-tooltip="tooltip" data-original-title="Start Break" type="submit">
                                        <i class="fa fa-coffee pr-2"></i><span>Start</span>
                                    </a>

                </div>
            </div>

        </div>
    </div>

    </div> 
</div> -->
<AddNewTags v-on:close="openTagsAdd(e)" v-if="addTagsModal" />
<Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message" v-bind:open="store.getAlertMessage.open"  v-on:close="store.$state.getMessageAlert.open=false"/>
<Overlap v-if="overLap"  :overLapList="store.$state.addManualResponse.viewString" v-on:close="overLap=false"/> 
</section>
    
</template>

<style>
.v-date-picker-header {
    display: none;
}

.vc-container.vc-blue {
    border: none;
    margin-top: 15px;
}
</style>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import {
    userTimerInfo
} from "../../stores/TimeTrack"
import Alert from '../Common/Alert.vue';
import moment from "moment"
import {
    getSecondsFromDate,
    timeToSeconds,
    formatter,
    dateAndTimeZone,
    dateTimeFromat,
    dateFormat,
} from "@/assets/common/vue-common.js"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import Overlap from './modals/Overlap.vue';
import VueTimepicker from 'vue2-timepicker'
import AddNewTags from "./modals/AddNewTags.vue"
import BreakModal from "../TimeTrack/modals/BreakModal.vue"
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import TimepickerInpute from "./TimepickerInpute.vue"
extend('required', {
    validate(value) {
        if (value.match(/^\s*$/)) {
            value = ''
        }
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    // This rule reports the `required` state of the field.
    computesRequired: true
});
export default defineComponent({
    name: "Manual Timer",
    components: {
        ValidationProvider,
        ValidationObserver,
        Alert,
        Overlap,
        VueTimepicker,
        AddNewTags,
        BreakModal,
        DatePicker,
        TimepickerInpute
    },
    data() {
        return {
            dateMask: {
                input: dateFormat,
            },
            menualTime: false,
            breakTimer: "00:00:00",
            breakTimerOn: false,
            textCountertlength: 2000,
            submitted: false,
            startTimerShow: false,
            timer: "00:00:00",
            error: "",
            timerPouse: false,
            timerOrBreak: false,
            submitted: false,
            timerPlay: false,
            breakModalPopup: false,
            formData: {},
            selectBreak: [],
            trackerData: {
                projectId: null,
                trackDescription: "",
                taskId: null,
                isBillable: true,
                format: false,
                date: null,
                trackId: 0,
                startTime: dateAndTimeZone(moment(), dateTimeFromat),
                stopTime: dateAndTimeZone(moment(), dateTimeFromat),
                companyId: null,
                currentDate: dateAndTimeZone(moment(), dateTimeFromat),
                trackAgent: "web",
                timerStatus: "start",
                CreatedBy: this.store.getUserInformation.id,
                createdBy: this.store.getUserInformation.id,
                linkedTrackId: 0,
                totalHours: 0,
                timeformat: "12",
                TagIds: null,
                isForcedUpdate: null,
                type: "TimeTrack",
                BreakDate: dateAndTimeZone(moment(), dateTimeFromat),
                BreakName: null,
                BreakId: null,
                BreakStartTime: 0,
                Shift_ID: 0,
                SelectedDate: "",
                Break_Shift: "",
                BreakStopTime: dateAndTimeZone(moment(), dateTimeFromat)
            },
            startTime: '00:00',
            endTime: '00:00',
            date: dateAndTimeZone(moment(), dateTimeFromat),
            FormSchema: [{
                layoutType: "single",
                Data: [{
                        astype: "MultiSelectField",
                        label: this.$t('AddBreak'),
                        name: "breakTime",
                        value: "",
                        placeholder: "",
                        config: {
                            options: this.store.getBreakTimeList,
                            showAddIcon: false,
                        },
                        // validationRules: "required",
                        validationRules: {
                            "required": true
                        },

                    },
                    {
                        astype: "SlotField",
                        label: '',
                        name: "button",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],
                            showAddIcon: false,
                            onChange: this.onChangeDropdown
                        },
                        validationRules: "",
                    },
                ]
            }],
            buttons: false,
            overLap: false,
            overLapData: [],
            tagShow: false,
            tagSelect: [],
            addTagsModal: false,
            breakData: {
                BreakDate: dateAndTimeZone(moment(), "YYYY-MM-DD"),
                BreakId: [],
                CurrentDate:dateAndTimeZone(moment(), "YYYY-MM-DD")
            },
            breakError: false,
            dateError: false
        }
    },
    created() {
        this.getInComplete();
        this.store.$state.tasks = []
    },
    mounted() {
        //This script use for tags and break modal auto off when user click any other actions
        document.addEventListener("click", (e) => {
            if (this.tagShow) {
                if (e.target == this.$refs.tags || e.target.parentNode == this.$refs.tags || e.target.parentNode.closest(".tagsModals") == this.$refs.tagsModals || e.target == this.$refs.tagsModals) return
                this.tagShow = false
            }
            if (this.breakModalPopup) {
                if (e.target == this.$refs.break || e.target.parentNode == this.$refs.break || e.target == this.$refs.breakModal || e.target.parentNode.closest(".breakModal") == this.$refs.breakModal) {
                    return
                } else {
                    this.breakModalPopup = false
                }
            }
        })

    },
    methods: {
        clearTags() {
            this.tagSelect = []
        },
        descriptionChange() {
            if (this.trackerData.trackDescription.match(/^\s*$/)) {
                this.trackerData.trackDescription = ''
            }
        },
        async getInComplete() {
            this.store.getBreakTime();
            this.store.tagGetForTimer();
            Object.assign(this.formData, this.trackerData);
            await this.store.getProjects()
            await this.store.getTimerInfo()
            this.store.$state.tasks = []
            this.store.$state.timerRunResponse = ''
            this.error = ''

            if (this.store.getTimer.projectId) {
                this.projectId()
                await this.store.loadTasks(this.store.getTimer.projectId);
                this.taskId()
            }
        },
        taskByProject() {
            this.trackerData.taskId = ''
            this.store.$state.tasks = []
            if (this.trackerData.projectId) {
                this.store.loadTasks(this.trackerData.projectId.projectId)
            } else {
                //getTasksFromProject = []
            }
            //this.store.$state.tasks = []
        },

        async submit() {
            if (this.validHours()) {
                this.submitted = true
                this.trackerData.projectId = this.trackerData.projectId ? this.trackerData.projectId.projectId : null
                this.trackerData.taskId = this.trackerData.taskId ? this.trackerData.taskId.value : null
                this.trackerData.isBillable = this.trackerData.isBillable ? 1 : 0
                this.trackerData.timeformat = this.trackerData.format ? 24 : 12
                //this.trackerData.startTime = moment(moment(this.date + ' '+this.startTime, 'hh:mm')).format("YYYY-MM-DD HH:mm:ss")
                // this.trackerData.stopTime = moment(moment(this.date + ' ' +this.endTime, 'hh:mm')).format("YYYY-MM-DD HH:mm:ss")
                this.trackerData.startDate = dateAndTimeZone(moment(), dateTimeFromat)
                this.trackerData.totalHours = this.timer
                this.trackerData.type = "TimeTrack"
                // this.trackerData.BreakId = []
                //     this.selectBreak.map((item)=>{
                //         this.trackerData.BreakId.push(item.value)
                //     })
                this.trackerData.TagIds = ''
                this.tagSelect.map((items, index) => {
                    this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
                })
                await this.store.addManualyTimer(this.trackerData);
                if (this.store.getMessage.status == "0" && this.store.getMessage.message != 'overlap') {
                    this.tagSelect = []
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)
                    const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                    this.store.getTrackedtimings(url1)
                    this.startTime = '00:00'
                    this.endTime = '00:00'
                    this.date = dateAndTimeZone(moment(), "YYYY-MM-DD")
                    this.$refs.form.reset()
                    Object.assign(this.trackerData, this.formData);
                    this.timer = "00:00:00"
                    //console.log("formmm", this.$refs.form)

                    if (this.store.getMessage.message == 'overlap') {
                        this.overLap = true
                    }
                } else {
                    this.trackerData.projectId = this.trackerData.projectId ? this.store.getProjectsByUser.find((data) => data.projectId == this.trackerData.projectId) : ''
                    this.trackerData.taskId = this.trackerData.taskId ? this.store.getTasksFromProject.find((data) => data.value == this.trackerData.taskId) : ''
                }
            }
            //await this.store.getTimerInfo();
            //this.projectId()
            //this.taskId()

        },
        textCounter() {

        },
        projectId() {
            this.trackerData.projectId = this.store.getTimer.projectId ? this.store.getProjectsByUser.find((data) => data.projectId == this.store.getTimer.projectId) : ''
        },
        taskId() {
            this.trackerData.taskId = this.store.getTimer.taskId ? this.store.getTasksFromProject.find((data) => data.value == this.store.getTimer.taskId) : ''
        },
        openTagsAdd(e) {
            this.addTagsModal = e
        },
        //Get isBillable status from task  is
        isBillableSatus() {
            this.trackerData.isBillable = this.trackerData.taskId.isBillable
            // console.log("checkk",this.trackerData.taskId.isBillable)
        },
        validHours() {
            let status = false
            if (!this.startTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": this.$t('PleaseEnterStartDate')
                })
            } else if (!this.endTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": this.$t('PleaseEnterEndDate')
                })
            }
            //console.log("end",moment(moment(this.endTime.HH+':'+this.endTime.mm, 'hh:mm')))
            else if (this.startTime && this.endTime) {
                this.date = moment(new Date(this.date), 'DD/MM/YYYY').format('YYYY-MM-DD')
                let startDateTime = moment(this.date + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(this.date + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                let today = moment(dateAndTimeZone(moment(), "YYYY-MM-DD HH:mm"))
                console.log("today", today.valueOf(), endDateTime.valueOf())
                // if (startDateTime.valueOf() > endDateTime.valueOf()) {
                //     this.store.alertBOxForMessage({
                //         "type": 'warning',
                //         "open": true,
                //         "message": this.$t('FutureTime')
                //     })
                // } else {
                    status = true
                    let sec = getSecondsFromDate({
                        startDate: moment(this.startTime, 'hh:mm'),
                        endDate: moment(this.endTime, 'hh:mm')
                    }, 'seconds')
                    this.trackerData.startTime = moment(startDateTime).format("YYYY-MM-DD HH:mm:ss")
                    this.trackerData.stopTime = moment(endDateTime).format("YYYY-MM-DD HH:mm:ss")

                    this.timer = formatter(sec)
                
            }
            return status
        },
        timerStart() {
            this.date = moment(new Date(this.date), 'DD/MM/YYYY').format('YYYY-MM-DD')
            let startDateTime = moment(this.date + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
            let endDateTime = moment(this.date + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
            if (startDateTime.valueOf() < endDateTime.valueOf()) {
                let sec = getSecondsFromDate({
                    startDate: startDateTime,
                    endDate: endDateTime
                }, 'seconds')
                this.timer = formatter(sec)
            } else {
                this.timer = "00:00:00"
            }
        },

        async breakSubmit() {
            if (this.selectBreak.length == 0) {
                this.breakError = true
            } else {
                this.breakError = false
            }
            if (this.breakData.BreakDate) {
                this.dateError = false
            } else {
                this.dateError = true
            }
            if (!this.breakError && !this.dateError) {

                this.breakData.BreakId = []
                this.selectBreak.map((item) => {
                    this.breakData.BreakId.push({
                        'breakId': item.value,
                        'isBillable': item.isPaid
                    })
                })
                this.breakData.CurrentDate=dateAndTimeZone(moment(), "YYYY-MM-DD")
                //this.breakData.isBillable = 
                await this.store.addManualBreak(this.breakData);
                if (this.store.getAddManualBreakResponse) {
                    this.breakModalPopup = false
                    this.selectBreak = []
                    this.breakData.BreakId = []
                    this.breakData.BreakDate = moment().format("YYYY-MM-DD")
                }

            }
        },
        breakModalOpen() {
            console.log("checvk", this.breakModalPopup)
            this.breakModalPopup = this.breakModalPopup = !this.breakModalPopup

        },
        dateSelected() {
            if (this.breakData.BreakDate) {
                this.dateError = false
            } else {
                this.dateError = true
            }
        },
        breakSelect() {
            if (this.selectBreak.length > 0) {
                this.breakError = false
            } else {
                this.breakError = true
            }
        }

    },
    setup() {
        const store = userTimerInfo();
        return {

            store,

        }
    },
})
</script>
<style  scoped>

input[type="date"]::-webkit-calendar-picker-indicator {
  position: absolute;
  left: 0;
}

input::-webkit-datetime-edit {
  position: relative;
  left: 15px;
}

input::-webkit-datetime-edit-fields-wrapper {
  position: relative;
  left: 15px;
}

</style><style>
.newddlist .dropdown input[type="text"] {
    border: 1px solid #abb5bf;
    width: 99%;
}

.dropdown-content {
    position: absolute;
    width: 100%;
    border: 1px solid #adadad;
    background: #fff;
    z-index: 9;
}

.timepickercontrol input {
    border: 1px solid #adadad;
    color: #505050;
}
</style>
