<template>
    
    <div class="timerDiv" :class="store.$state.breakTimerCountInSec < 0 ? 'exceed' : ''" :style="'border: 3px solid #f0f0f0; borderImage:'+url">
        <div  id="TimerCanves"
                                        class="DivTimer timerclck"
                                        :class="store.$state.breakTimerCountInSec < 0 ? 'danger' : 'success'"
                                        >
                                        {{ store.getBreakTimerClock }}
                                    </div>
    </div>
</template>

<style>

.timerDiv{padding:5px; margin: 4px;}
.timerDiv .timerclck{ margin:0px}
.timerDiv.exceed{border:3px solid #dc3546!important}
</style>


<script>
import {
    defineComponent,
} from '@vue/composition-api'
export default defineComponent({
    name: "Timer Add",
    components: {
    },
    props: {
      'seconds': {
        type: Number,
        default: 0
      },
     'store':{
        type:Object
     }
    },
    data () {
      return {
        spaces: '',
        inner: 0,
        intervalCtrl: null,
        url:'',
      }
    },
    computed: {
      computedSVGUrl: function () {
        return window.btoa(`<svg width='100' height='100' viewBox='0 0 100 100' fill='#ff0000' xmlns='http://www.w3.org/2000/svg'> 
            <style>path{animation:stroke ${this.seconds}s linear;}@keyframes stroke{to{stroke-dashoffset:388;}}</style>
            <linearGradient id='g' x1='0%' y1='0%' x2='0%' y2='100%'>
                <stop offset='0%' stop-color='#659940' />
                <stop offset='25%' stop-color='#659940' />
                <stop offset='50%' stop-color='#659940' />
                <stop offset='100%' stop-color='#659940' />
                </linearGradient> 
                <path d='M1.5 1.5 l97 0l0 97l-97 0 l0 -97' stroke-linecap='square' stroke='url(#g)' stroke-width='3' stroke-dasharray='388'/>
                 ${this.spaces} </svg>`)
      }
    },
    watch: {
      seconds: {
        handler: function (newVal) {
          this.inner = newVal
          this.startTimer(newVal)
          this.store.$state.breakTimerCountInSec < 0 ? this.resetInterval(): ''
        },
        immediate: true
      }
    },
    mounted: function () {
      ///this.startTimer(this.seconds)
      this.timeStart()
      //this.url = `url("data:image/svg+xml;base64,${this.computedSVGUrl}") 1`
    },
    methods: {
        timeStart(){
            console.log("checkkk")
            this.inner =  this.seconds
            this.spaces += ' '
            this.startTimer(this.seconds)
            
        },
      startTimer: function (seconds) {
        this.resetInterval()
        this.intervalCtrl = setInterval(() => {
            this.url = `url("data:image/svg+xml;base64,${this.computedSVGUrl}") 1`
          this.inner -= 1
          this.inner <= 0 && this.resetInterval()
        }, 1000)
      },
      resetInterval: function () {
        this.intervalCtrl && clearInterval(this.intervalCtrl)
        this.intervalCtrl = null
      }
    }
  })
      

  </script>

 