<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">

                <div class="modal-dialog ui-draggable" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{ this.userName }} : {{ $t('Status') }} </h5><button
                                type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i
                                    class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <!-- {{ UserTaskViewData }} -->
                        <div class="listing mt-0 p-3">
                        <div class="table-responsive"> <!-- style=" display: contents; float: left;width: 100%;overflow: auto;" -->

                            <table class="table table-bordered mt-0 mb-0 newtimertable">
                            <tr v-for="(item, index) in UserTaskViewData"
                                :key="index">

                                <!-- <td class="tabchild" style="width:60px;">
                                    <div style="text-align: center;">
                                        {{ GetCurrentDate(item.TRACK_START_TIME) }}
                                    </div>
                                </td> -->

                                <td class="bg_outerdark teamstatus" style="width:80px;">
                                    <div class="text-center ">
                                       <span class="float-left w-100 font-weight-bold font-23 mt-2" style="line-height: 18px;">{{GetCurrentDate(item.TRACK_START_TIME).split(' ')[0]}}</span>
                                       <span class="float-left w-100 font-weight-bold font-14">{{GetCurrentDate(item.TRACK_START_TIME).split(' ')[1]}}</span>
                                    </div>
                                </td>
                                <td>
                                    <div class="d-inline-block">
                                        <span title="" value="-1"  class="text-dark" >
                                            <span class="d-block font-14 font-weight-medium">{{ item.PROJECT_NAME }} :{{
                                                item.TASK_NAME }}</span>
                                            <span class="text-dark d-block font-12">{{ item.TRACK_DESCRIPTION }}</span></span>
                                    </div>
                                </td>

                                <td style="max-width:100px; width: 100px;">
                                    <div class="dottedbod_rt w-100 ">
                                        <div class="float-left">
                                            <i
                                                :class="{ 'tt_lisitng_icons ico_listdollar float-left': (item.BILLABLE_MARK == '$'), 'tt_lisitng_icons ico_listdollar-o float-left': (item.BILLABLE_MARK != '$') }"></i>
                                        </div>
                                    </div>
                                </td>
                                <td class="timetracked" style="max-width:140px; width: 140px;">
                                <!-- <span :class="{ 'mt-1 green': (item.BILLABLE_MARK == '$'), 'mt-1': (item.BILLABLE_MARK != '$') }">{{
                                    convertToTimeFormatHHMM(item.TrackHours) }}</span> -->
                                    <span class="mt-1">{{ convertToTimeFormatHHMM(item.TrackHours) }}</span>
                                    </td>
                            </tr>
                            </table>
                       
                        </div>

                        <Pager :numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPageNumber" prev-text="Prev" first-text="First" last-text="Last" next-text="Next"   @SetCurrentPage="pagerMethod"></Pager>
                    </div>
                </div>
                </div>
            </div>
        </div>
        <!-- <pager v-bind:numberofrecords="TotalRecordsUserTaskView" :CurrentPageNumber="CurrentPageNumber" prev-text="Prev"
        first-text="First" last-text="Last" next-text="Next" v-bind:callbackfunction="callbackfunction"></pager>  -->
    </div>
     
</template>
    
<script>
import {
    defineComponent,
} from '@vue/composition-api';
import moment from 'moment';
import Pager from '../../Common/Pager.vue'
import DataService from '@/services/DataService';
export default defineComponent({
    name: "UserTaskView",
    components: {
        Pager
    },
    props: {
        store: {
            type: Object
        }
        ,
        callbackfunction:{
            type: Object
        },
        
        TotalRecordsUserTaskView: {
            type: Number
        }
        ,
        CurrentPageNumber: {
            type: Number
        }
    },

    data() {
        return {
            UserTaskViewData: [],
            userName: '',
            TotalRecords:0,
            TotalPages:0,
            PageSize:10,
            PageNumber:1,
            UserId:0
            // CurrentPageNumber: 0,
            // TotalRecordsUserTaskView: 0,
            // FetchData: this.$parent.FetchUserTaskView,
            // PagerFetchData: this.$parent.pagerMethodUserTaskView,
        }
    },
    created: function () {
        debugger
        this.userName = this.$parent.userNameTaskView;
        this.UserTaskViewData = this.$parent.userTaskViewDetail;
        this.UserId = this.$parent.userTaskUserId;
        this.SetAllData();
        // this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
        // this.PageNumber = this.CurrentPageNumber;
        // this.TotalRecordsUserTaskView = this.$parent.userTaskViewTotalRecord;
    },
    methods: {
//         callbackfunction(event){
// console.log("event",event)
//         },
        GetCurrentDate: function (date) {
            const getDate = moment(new Date(date), 'DD/MM/YYYY').format('YYYY-MM-DD')
            const Yesterday = moment().subtract(1, 'day').format('YYYY-MM-DD')
            const today = moment().endOf('day').format('YYYY-MM-DD')
            // if (getDate == today) return 'Today'
            return moment(new Date(date), 'DD/MM/YYYY').format('DD MMM')
        },
        convertToTimeFormatHHMM: function (seconds) {
            if (seconds === 0 || seconds < 0) {
                return "00 H 00 M";
            } else {
                const hours = Math.floor(seconds / 3600);
                const minutes = Math.floor((seconds % 3600) / 60);
                const remainingSeconds = seconds % 60;

                const formattedHours = hours < 10 ? `0${hours}` : hours;
                const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;

                return `${formattedHours}h ${formattedMinutes}m`


                // return `${hours}h ${minutes}m`;
            }
        },
        SetAllData(){
            this.TotalRecords = this.TotalRecordsUserTaskView;
            this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
            var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}&userId=${this.UserId}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}`;
            console.log('modal user task view params',params);    
             DataService.GetTimerUserTaskView(params).then(response => {
                    if (response.data != null && response.data != "") {

                        console.log('response.data.result.USERTASKVIEW',response.data.result.USERTASKVIEW);

                    this.UserTaskViewData =  response.data.result.USERTASKVIEW;
                    this.TotalRecords =  response.data.result.TOTAL_COUNT;
                    
                    }
                });
        },
        pagerMethod(value){
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.SetAllData();
        }
    },
});

</script>
<style scoped>
.action-list-box {
    opacity: 0;
    height: 0px;
    transition: opacity 1s;
}

.is-visible {
    display: inline-block;
    height: auto;
    overflow: hidden !important;
}
</style>
    