<template>
    <section class="activity-tab col-md-12 p-0 mt-4 float-left">
        <div>

            <!-- <button class="tablinks" @click="teamStatus">{{ $t('MyStatus') }}</button>
<button class="tablinks" @click="myStatus">{{ $t('Teamstatus') }}</button> -->

            <div class="tab_container tab-style newtimer mb-0">
                <div class="responsive-tabs-container accordion-xs accordion-sm">
                    <ul id="myTab" role="tablist"
                        class="nav nav-tabs all-tabs responsive-tabs mt-2 responivecustomtab HideTab w-100">
                        <li class="nav-item">
                            <a @click="myStatus('MyStatus')" data-toggle="tab" role="tab" id="tab-Details"
                                class="nav-link productCreateTab1 active" aria-selected="true"><em
                                    class="fa fa-user mr-1"></em>
                                {{ $t('MyStatus') }}</a>
                        </li>
                        <li class="nav-item">
                            <a @click="myStatus('TeamStatus')" data-toggle="tab" role="tab" id="tab-Details"
                                class="nav-link productCreateTab1" aria-selected="true"><em class="fa fa-users mr-1"></em>
                                {{ $t('Teamstatus') }}</a>
                        </li>
                    </ul>
                </div>
            </div>

        </div>

        <div v-if="myStatusShow == 'MyStatus'" class="col-lg-12 p-0 float-left p-3 border newtimertabsbase">
            <div class="row weeklystatusboxcontrol">
                <div class="col-md-4 mb-3">
                    <div class="border calbx eqcol h-100">
                        <div class="month-heading" style="position: relative;">
                            <h3>{{ formatOnlyMonthYearWithComma(WeekDate) }}</h3>
                            <!-- <div class="btnbase">
                                <a href="javascript:void(0)"
                                    v-on:click="CounterDecrement('-', WeekDayCount, WeekDayCount += 1)"
                                    class="btnsnextprev"><em class="fa fa-angle-double-left mr-1"></em>Previous</a><a
                                    href="javascript:void(0)" v-if="WeekDayCount > 0"
                                    v-on:click="CounterIncrement('-', WeekDayCountInCrease += 1)" class="btnsnextprev">{{
                                        $t('Next') }}<em class="fa fa-angle-double-right ml-1"></em></a>
                            </div> -->
                        </div>
                        <div class="month_day">
                            <ul>
                                <li class="d-none">{{ TimeTrackList.length }}</li>
                                <li v-for="(trackGroup, indexTimeTrack)  in TimeTrackList" :key="indexTimeTrack">
                                    <div class="date_fm date_fm_hs2">
                                        <h4>{{ formatOnlyDate(trackGroup.WeekDate) }}</h4>
                                        <p>{{ formatOnlyDay(trackGroup.WeekDate) }}</p>
                                    </div>

                                    <div v-if="indexTimeTrack == 5 && trackGroup.TotalHours == null"
                                        v-bind:class="{ 'darkyellow lb_hs2': indexTimeTrack == 5 && trackGroup.TotalHours == null }"
                                        class="">
                                        <p>{{ $t('weekly') }} <br />{{ $t('Off') }}</p>
                                    </div>

                                    <div v-else-if="indexTimeTrack == 6 && trackGroup.TotalHours == null"
                                        v-bind:class="{ 'lightyellow lb_hs2': indexTimeTrack == 6 && trackGroup.TotalHours == null }"
                                        class="">
                                        <p>{{ $t('weekly') }} <br />{{ $t('Off') }}</p>
                                    </div>

                                    <div v-else class="lightblue lb_hs2">
                                        <h6>{{ $t('TimeTracked') }}</h6>
                                        <h6>{{ trackGroup.TotalHours > 0 ? convertSecondsToHHMM(trackGroup.TotalHours) : 0
                                        }}</h6>
                                        <h6>hours</h6>
                                    </div>
                                </li>

                            </ul>
                        </div>

                    </div>
                </div>
                <div class="col-md-4 mb-3 overflow-hidden">
                    <div class="border infotrack eqcol h-100 row bg-white">

                        <div class="trackinfo ">
                            <h1 class="time_info">{{ convertSecondsToHHMM(TotalWeekHours) }} {{ $t('HrsSmall') }}</h1>
                            <h3>{{ $t('TotalTimeTracked') }}</h3>
                            <p class="mb-4">Avg. Per Day -
                                {{ convertSecondsToHHMM(calculateWeekHoursAVG(TotalWeekHours, WeekDays)) }}
                                {{ $t('HrsSmall') }}</p>
                            <div class="containerLegendTimeTrack containerLegend100_WS uniquefixHS w-100"></div>
                            
                            <div id="chart " v-if="myStatusWeekChartShow">
                                <VueApexCharts type="bar" height="150" :options="chartOptionsBar" :series="seriesbar">
                                </VueApexCharts>
                            </div>

                        </div>
                        <div class="col-img text-center track-hours-chart " v-if="myStatusWeekChartShow">
                            <VueApexCharts type="pie" width="800" height="300" :options="myStatusWeekchartOptions" :series="myStatusWeekseries" />
                        </div>
                    </div>
                </div>
                <div class="col-md-4 mb-3 ">

                    <div class="border infotrack eqcol h-100 overflow-hidden bg-white">

                        <div class="trackinfo trackinfo_left2" style="min-height: 270px;">
                            <h2 class="time_info mb-4">{{ $t('OverAllTask') }}</h2>
                            <div>
                                <ul>
                                    <li class="txt_overdue"><span><b>{{ OverdueRecords }}</b></span> {{ $t('OVERDUE') }}
                                    </li>
                                    <li class="txt_pending"><span><b>{{ PendingRecords }}</b></span> {{ $t('Pending') }}
                                    </li>
                                    <li class="txt_inprogress"><span><b>{{ ProgressRecords }}</b></span>
                                        {{ $t('Inprogress') }} </li>
                                    <li class="txt_progress"><span><b>{{ CompletedRecords }}</b></span> {{ $t('Completed')
                                    }}
                                    </li>
                                    <li class="txt_to-upcoming"><span><b>{{ UpcomingRecords }}</b></span> {{ $t('Upcoming')
                                    }}
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div class="col-img text-center track-hours-chart trackinfo_right2" v-if="showPieWeekly">
                            <VueApexCharts type="pie" class="text-left" width="800" height="300" :options="chartOptions1"
                                :series="series1" />
                        </div>
                    </div>
                </div>

            </div>
        </div>
        <div v-if="myStatusShow == 'TeamStatus'" class="col-lg-12 p-0 float-left border p-3 newtimertabsbase">

            <div class="container-fluid p-0">

                <div class="float-left w-100 col-md-12 p-0">
                    <div class="col-12 float-left p-0">
                        <div class="leftstatus">

                            <div class="col-12 float-left border bg-light p-0 mb-3"
                                v-for="(item, index) in store.getTimerTeamStatusState.TRACKTIME" :key="index">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-6 float-left bg-white team-con-box">
                                    <div class="team-con-img">
                                        <img class="img-circle avatar" :src="item.Avatar" width="70" height="70"
                                            alt="Image">
                                            
                                        <div class="online"></div>
                                    </div>
                                    <div class="team-con-content">
                                        <div class="names font-weight-medium">{{ item.userName }} <em
                                                class="fa fa-globe ml-1"></em></div>

                                        <div class="col-md-12 float-left mt-1 p-0">

                                            <div class="col-md-3 float-left p-0">
                                                <div class="barsbase w-100 float-left weektracked">
                                                    <div class="iconbase"><em class="fa fa-clock-o"></em></div>
                                                    <div class="icontext">
                                                        <span class="w-100 float-left font-weight-medium font-14">{{
                                                            item.TrackTimer }}m</span>
                                                        <span class="w-100 float-left font-12"> {{ $t('Weeklytracked') }}
                                                        </span>
                                                    </div>

                                                </div>
                                            </div>

                                            <div class="col-md-3 float-left p-0">
                                                <div class="barsbase w-100 float-left auto">
                                                    <div class="iconbase"><em class="fa fa-circle-o-notch"></em></div>
                                                    <div class="icontext">
                                                        <span class="w-100 float-left font-weight-medium font-14">{{
                                                            item.Automatic }}%</span>
                                                        <span class="w-100 float-left font-12">Auto</span>
                                                    </div>

                                                </div>
                                            </div>

                                            <div class="col-md-3 float-left p-0">
                                                <div class="barsbase w-100 float-left manual">
                                                    <div class="iconbase"><em class="fa fa-clock-o"></em></div>
                                                    <div class="icontext">
                                                        <span class="w-100 float-left font-weight-medium font-14">{{
                                                            item.Manual }}%</span>
                                                        <span class="w-100 float-left font-12">{{ $t('Manual') }}</span>
                                                    </div>

                                                </div>
                                            </div>

                                            <div class="col-md-3 float-left p-0">
                                                <div class="barsbase w-100 float-left efficiency">
                                                    <div class="iconbase"><em class="fa fa-clock-o"></em></div>
                                                    <div class="icontext">
                                                        <span class="w-100 float-left font-weight-medium font-14">{{
                                                            item.Efficiency }}%</span>
                                                        <span class="w-100 float-left font-12">{{ $t('Efficiency') }}</span>
                                                    </div>

                                                </div>
                                            </div>
                                            <div class="progress w-100">

                                                <div class="progress-bar" role="progressbar manual"
                                                    :style="'width: ' + item.Automatic + '%;background-color: #f7a82f;'"
                                                    aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>

                                                <div class="progress-bar" role="progressbar"
                                                    :style="'width:' + item.Manual + '%;background-color: #00b298;'"
                                                    aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                                                </div>
                                            </div>

                                        </div>

                                    </div>
                                </div>

                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-6 float-left">
                                    <div class="team-con-rangecontent mt-4">
                                        <span class="w-100 float-left font-weight-medium">{{ item.ProjectName }}</span>
                                        <span class="w-100 float-left">{{ item.TaskDetail }}</span>
                                        <a class="collapsed" data-toggle="collapse"
                                            @click="openUserTaskViewModal(true, item.Userid, item.userName)"
                                            aria-expanded="true" aria-controls="collapseOne1"><span
                                                class="w-100 float-left">view more</span></a>
                                    </div>
                                    <div class="team-con-range mt-2">
                                        <div id="chart" class="team" v-if="showPieChart">

                                            <VueApexCharts type="donut" height="240" width="240"
                                                :options="chartOptions2[index]" :series="series2[index]"></VueApexCharts>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-6 float-left">

                                </div>

                            </div>

                        </div>

                        <div class="rightgraph bg-white border">
                            <div class="eqcol h-100 row p-4">
                                <div class="trackinfo">
                                    <h2 class="time_info">{{ convertSecondsToHHMMSS(teamstatusTotalhours) }} Hrs</h2>
                                    <h5 class="text-secondary">{{ $t('TotalTimeTracked') }}</h5>
                                    <p class="mb-4">{{ $t('AvgPerDay') }}
                                        {{ convertSecondsToHHMMSS(teamstatusavgtime) }} Hrs
                                    </p>
                                    <div class="containerLegendTimeTrack containerLegend100_WS uniquefixHS w-100"></div>
                                    <div id="chart" v-if="showPieChart">
                                        <VueApexCharts type="bar" height="150" :options="chartOptionsBarTeamstatus"
                                            :series="seriesbarTeamstatus">
                                        </VueApexCharts>
                                    </div>

                                </div>
                                <div class="col-img text-center track-hours-chart team12" v-if="showPieChart">
                                    <VueApexCharts class="text-left" style="margin-top: 50px; " type="pie" width="350"
                                        height="300" :options="chartOptions" :series="series" />
                                </div>
                            </div>
                        </div>

                    </div>

                </div>

            </div>

        </div>
        <UserTaskView v-if="userTaskViewShow" :callbackfunction="FetchUserTaskView"
            :TotalRecordsUserTaskView="userTaskViewTotalRecord" :CurrentPageNumber="PageNumber"
            v-on:close="userTaskViewShow = false" v-bind:store="store" />
        <!-- <UserTaskView v-if="userTaskViewShow" v-on:close="userTaskViewShow=false" v-bind:store="store"/> -->
    </section>
</template>

<script>
import DataService from '@/services/DataService';
import moment from 'moment';
import {
    formatter,
    convertSecondToHHMM,
    dateAndTimeZone
} from "@/assets/common/vue-common.js"
import VueApexCharts from 'vue-apexcharts'
import UserTaskView from "@/components/TimeTrack/modals/modalUserTaskView.vue"
export default {
    components: {
        VueApexCharts,
        UserTaskView
    },
    props: {
        store: {
            type: Object
        },
    },
    data() {
        return {
            TimeTrackList: [],
            WeekDayCount: 0,
            WeekDayCountInCrease: 0,
            WeekDayInCreaseDecrease: 0,
            isActiveWeeklyStatus: true,
            PROGRESS: '',
            PendingRecords: 0,
            ProgressRecords: 0,
            OverdueRecords: 0,
            UpcomingRecords: 0,
            CompletedRecords: 0,
            chartData: [],
            pieCharLable: [],
            series: [],
            series1: [],
            series2: [],
            chartOptions: {

            },
            chartOptions1: {

            },
            myStatusWeekchartOptions:{

            },
            chartOptionsBarTeamstatus:{

            },
            myStatusWeekseries:[],
            chartOptions2: [],
            seriesbar: [],
            seriesbarTeamstatus:[],
            chartOptionsBar: {},
            taskList: [],
            myStatusShow: 'MyStatus',
            teamStatus: [],
            projectDetails: [],
            userTaskViewShow: false,
            userNameTaskView: '',
            userTaskViewDetail: [],
            userTaskViewTotalRecord: 0,
            PageSize: 10,
            PageNumber: 1,
            privilegeParams: null,
            userTaskUserId: 0,
            showPieChart: false,
            myStatusWeekChartShow:false,
            showPieWeekly: false,
            teamstatusTotalhours:0,
            teamstatusavgtime:0,
            WeekDays:0,
            TotalWeekHours:0,
            WeekDate:'',
            From: '',
            To: ''

        }
    },
    created() {
        this.privilegeParams = `controller=Timer&action=`;
        this.TimeTrackWeeklyDataList();
        this.TimerWeeklyTask();
        //this.TimerWeeklyTeamStatus();
    },
    watch: {
    'store.$state.dateFilterType': {
        immediate: true,
        handler() {
           // update locally relevant data
           console.log("store.$state.dateFilterType",this.store.$state.dateFilterType)
           this.TimeTrackWeeklyDataList();
        }
     }
  },
    methods: {
        toggleWeeklyStatus() {
            if (!this.isActiveWeeklyStatus) {
                this.isActiveWeeklyStatus = true;
            } else {
                this.isActiveWeeklyStatus = false;
            }
        },
        formatOnlyMonthYearWithComma: function (value) {
            //debugger;
            if (value) {
                return moment(value).format('MMM') + ', ' + moment(value).format('YYYY');
            }
        },
        formatOnlyDay: function (value) {

            if (value) {
                // return moment(value).format('ddd').toUpperCase();
                return moment(value).format('ddd').toUpperCase();
            }
        },
        CounterDecrement: function (value) {
            var vm = this;
            vm.WeekDayInCreaseDecrease = value + vm.WeekDayCount;
            vm.TimeTrackWeeklyDataList();
        },
        CounterIncrement: function (value) {
            var vm = this;
            var increaseValue = parseInt(vm.WeekDayCount) - parseInt(vm.WeekDayCountInCrease);
            vm.WeekDayInCreaseDecrease = value + increaseValue;
            if (String(vm.WeekDayInCreaseDecrease).indexOf("--") !== -1) {
                vm.WeekDayInCreaseDecrease = 0;
            }
            if (vm.WeekDayInCreaseDecrease == 0) {
                vm.WeekDayCount = 0;
            }
            vm.TimeTrackWeeklyDataList();
        },
        convertSecondsToHHMMSS: function (intSecondsToConvert) {
            if (intSecondsToConvert > 0) {
                return formatter(intSecondsToConvert)
            } else {
                return "00:00:00"
            }

        },
        formatOnlyDate: function (value) {
            if (value) {
                return moment(value).format('DD');
            }
        },
        calculateWeekHoursAVG: function (totalHours, days) {
            var avgPerWeekHours = Math.floor(totalHours / days);
            if (isNaN(avgPerWeekHours)) {
                avgPerWeekHours = 0;
            }
            return avgPerWeekHours
        },
        async TimeTrackWeeklyDataList() {
            var vm = this;
            //showLoader();
            var url = "weekDay=" + vm.WeekDayInCreaseDecrease
            let seriesbarData = [{
                "name": "Time",
                data: [],

            }]
            let seriesData = []
            let seriesDonutData = []
            let data = []
            let pieCharLableData = []
            this.myStatusWeekChartShow = false
             if(this.store.$state.dateFilterType=='Today' || this.store.$state.dateFilterType=='Yesterday'){
                
               await this.GetStartEndDates();
                
             }
             else{
                this.From=this.store.$state.From
                this.To=this.store.$state.To
             }
             console.log("from1",vm.From);
                console.log("To1",vm.To);
            var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.To}&fromDate=${this.From}`
            //await this.store.TimerMyStatus(params)
             await DataService.TimerMyStatus(params).then(response => {
                // console.log("TimerMyStatus",response);
                if (response.data != null && response.data != "") {
                    vm.noRecordToDoList = false;
                    vm.TimeTrackList = [];
                    vm.TimeTrackList = response.data.result.DATA;
                    var sum = 0;
                   // var WeekDays1 = 0;
                    var TimeTrackListNew = [];
                    //vm.seriesbar = []
                    vm.TimeTrackList.map((item, index) => {

                        var objWeekDay = {};
                        //WeekDays1 = index + 1;
                        vm.WeekDate = item.WeekDate;
                        var weekDay = vm.formatOnlyDay(item.WeekDate);
                        if (item.TotalHours != null) {
                            sum = parseInt(sum) + parseInt(item.TotalHours);

                            objWeekDay = {
                                Name: weekDay,
                                Value: item.TotalHours,
                            };
                            // TimeTrackListNew.push(objWeekDay)
                        }
                        seriesData.push(item.TotalHours ? objWeekDay.Value : 0);
                        seriesbarData[0].data.push(item.TotalHours ? objWeekDay.Value : 0);
                        //vm.seriesbar.push(objWeekDay);
                        pieCharLableData.push(weekDay)
                    })

                    //console.log("test",vm.WeekDate,vm.WeekDays)
                    vm.TotalWeekHours = sum;
                }
           });
            this.myStatusWeekpieChart(pieCharLableData, seriesData)
            //console.log("checkkkkkk",vm.pieCharLableData)
            this.lineChart(pieCharLableData, seriesbarData)
                this.myStatusWeekChartShow = true
        },
        async TimerWeeklyTask() {
            var vm = this;
            let keys = [];
            let values = [];
            this.showPieWeekly = false
           await DataService.TimerWeeklyTask().then(response => {

                if (response.data != null && response.data != "") {
                    // console.log("data",response.data.result)
                    vm.OverdueRecords = response.data.result.OVERDUE;
                    vm.CompletedRecords = response.data.result.COMPLETED;
                    vm.PendingRecords = response.data.result.PENDING;
                    vm.ProgressRecords = response.data.result.INPROGRESS;
                    vm.UpcomingRecords = response.data.result.UPCOMING
                    // data = [vm.OverdueRecords,vm.CompletedRecords,vm.PendingRecords,vm.ProgressRecords,vm.UpcomingRecords]
                    // label=['OverdueRecords','CompletedRecords',PendingRecords,ProgressRecords,UpcomingRecords]
                    keys = Object.keys(response.data.result);
                    values = Object.values(response.data.result);
                    // console.log("Keys:", keys);
                     console.log("Values:", values);
                }
                this.pieChart1(keys, values)
                this.showPieWeekly = true

            });

        },
       
        myStatus(value) {
            if(value == 'TeamStatus'){
                this.TimerWeeklyTeamStatus();
                this.store.$state.headingInactivityTab =   'ActivitiesDoneByTeam'
            }else{
                this.store.$state.headingInactivityTab = 'ActivitiesDoneByYou'
            }
            this.myStatusShow = value;
        },
        async TimerWeeklyTeamStatus() {
            var vm =this;
            let seriesbarData = [{
                "name": "Time",
                data: [],

            }]
            this.showPieChart = false
            var sum = 0;
            let seriesData = []
            let pieCharLableData = []
            if(this.store.$state.dateFilterType=='Today' || this.store.$state.dateFilterType=='Yesterday'){
                
                await this.GetStartEndDates();
                 this.store.$state.From=this.From
                 this.store.$state.To=this.To
              }
             
            var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}`
            await this.store.TimerTeamStatus(params)
            console.log("totalHours", this.store.getTimerTeamStatusState.TOTALTRACKHOURS)
            this.store.getTimerTeamStatusState.TOTALTRACKHOURS ?  this.store.getTimerTeamStatusState.TOTALTRACKHOURS.map((item, index) => {

                var objWeekDay = {};
                console.log("itemmmm",item)
                vm.WeekDays = index + 1;
                
                vm.WeekDate = item.Date;
               
                var weekDay = vm.formatOnlyDay(item.Date);
                
                if (item.Trackhours != null) {
                    vm.teamstatusTotalhours=item.TotalTrackHours
                    vm.teamstatusavgtime=item.AvgPerday
                    objWeekDay = {
                        Name: weekDay,
                        Value: item.Trackhours,
                    };
                    // TimeTrackListNew.push(objWeekDay)
                }
               
                console.log("item.TotalTrackHours",item)
                seriesData.push(item.Trackhours ? objWeekDay.Value : 0);
                seriesbarData[0].data.push(item.Trackhours ? objWeekDay.Value : 0);
                pieCharLableData.push(weekDay)
                
            }) : ''
           console.log("timetrack",this.store.getTimerTeamStatusState.TRACKTIME) 
            this.store.getTimerTeamStatusState.TRACKTIME.map((item, index) => {
                this.donutChart([item.Automatic ? item.Automatic : 0, item.Manual ? item.Manual : 0], index, item.TrackHours, [this.$t('Automatic'), this.$t('Manual')])
            })
            console.log("seriesData",seriesData)
            this.pieChart(pieCharLableData, seriesData)
            this.lineChartTeamstatus(pieCharLableData, seriesbarData)
            setTimeout(() => {
                this.showPieChart = true
            }, 300)
            const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'Graph'}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}&graphFor=${'teamstatus'}`
                    this.store.getTrackedtimings(url1)

        },
        openUserTaskViewModal(isshowmodal, userid, username) {
            // this.userTaskViewDetail = [];
            DataService.CheckPrivilege(this.privilegeParams + 'TeamStatusDetails').then(response => {
                if (!response.data) {
                    this.ShowAlert(this.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                } else {
                    this.userNameTaskView = username;
                    if (userid > 0) {
                        this.FetchUserTaskView(userid);
                        this.userTaskViewShow = isshowmodal;
                    }
                }
            })

        },
        async FetchUserTaskView(userid) {
            var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}&userId=${userid}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}`;
            console.log('params', params);
            this.userTaskUserId = userid;
            await DataService.GetTimerUserTaskView(params).then(response => {
                if (response.data != null && response.data != "") {
                    this.userTaskViewDetail = response.data.result.USERTASKVIEW;
                    this.userTaskViewTotalRecord = response.data.result.TOTAL_COUNT;
                }
            });
        },
        pagerMethodUserTaskView: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchUserTaskView();
        },
        donutChart(series, index, data, lable) {
            this.series2[index] = series
            this.chartOptions2[index] = {
                chart: {
                    width: '150px',
                    type: 'donut',
                },
                stroke: {
                    width: 0,
                },
                plotOptions: {
                    pie: {
                        donut: {
                            labels: {
                                show: true,
                                name: {
                                    show: false,
                                },
                                total: {
                                    show: true,
                                    showAlways: true,

                                    formatter: function () {
                                        return data
                                    }
                                }
                            }
                        }
                    }
                },
                tooltip: {
                    custom: function ({
                        series,
                        seriesIndex,
                        dataPointIndex,
                        w
