        GetStartEndDates: async function () {
            var VueObj = this;
            let url = `DateSpan=thisweek&report=`;
            await DataService.GetStartEndDate(url).then(response => {
                console.log("GetStartEndDate",response)
                VueObj.From = response.data.dateStart;
                VueObj.To = response.data.dateEnd;
                VueObj.StartWeekDay = response.data.startWeekDay;
                
            });
          console.log("VueObj.startWeekDay",VueObj.StartWeekDay)
            var offset = 1;
            switch (VueObj.StartWeekDay.toLowerCase()) {
                case 'tuesday':
                    offset = 2;
                    break;
                case 'wednesday':
                    offset = 3;
                    break;
                case 'thursday':
                    offset = 4;
                    break;
                case 'friday':
                    offset = 5;
                    break;
                case 'satureday':
                    offset = 6;
                    break;
                case 'sunday':
                    offset = 0;
                    break;
                default:
                    offset = 1;
                    break;
            }
            var dateGet = new Date()
            if(this.store.$state.dateFilterType== 'Yesterday'){
                dateGet = new Date(moment(dateAndTimeZone(moment())).subtract(1, 'd'))
            }else {
                dateGet =  new Date(moment(dateAndTimeZone(moment())))
            }
           
            var currentDay = dateGet.getDay() - offset;
            console.log("dateTime", currentDay);
            var startdate = new Date(dateGet.getFullYear(), dateGet.getMonth(), dateGet.getDate() - currentDay);
            var enddate = new Date(dateGet.getFullYear(), dateGet.getMonth(), dateGet.getDate() - currentDay + 6);
            var dateTime = VueObj.$options.filters.formatDate(startdate) + '-' + VueObj.$options.filters.formatDate(enddate);
            console.log("dateTime", dateTime)
            VueObj.From = VueObj.$options.filters.formatDate(startdate);
            VueObj.To = VueObj.$options.filters.formatDate(enddate);
            console.log("from", VueObj.From);
                console.log("To", VueObj.From);

