<template lang="en">
<div>

    <div v-if="automaticShowBreak">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3 p-0">
            <div class="heading-border">
                <h5 class="h5"><span id="SProjectDetailTitleSpan">{{$t('Break_MyDashboard_Module')}}</span>
                    <a class="link float-right font-14 mt-1" @click="changeBreakModal"><em class="fa fa-plus mr-2"></em>Add Manual</a></h5>
            </div>
        </div>

        <!-- <button type="button" class="close" style="background: none;color: #fff !important;" @click="$emit('close',{breakTimer:false,modalClose:false})"><em aria-hidden="true" class="fa fa-times"></em></button> -->
        <div class="col-12 p-0 float-left" id="divModalTimetrack">
            <!-- <dynamic-form lang="en" :buttons="buttons" ref="form" :schema="FormSchema" v-on:OnSubmit="submit()">
                <template v-slot:tgslot-button="{ data }">

                    </template>
            </dynamic-form> -->
<label>{{$t('selectBreak') }}<span class="text-danger">*</span></label>
<div class="w-100 d-flex dynamic-multiselect" id="divModalTimetrack">
                <multiselect @input="singleBreak" :placeholder="$t('selectBreak')" :multiple="false" class="multiselect p-0 w-100" v-model="singleBreakSelect" :options="store.getBreakTimeList" label="name" track-by="value" :show-labels="false" id="exampleFormControlSelect1">
                    <template slot="option" slot-scope="props">
                        <div class="option__desc"><span class="option__title">{{
                                                props.option.name }} ({{ props.option.breakDuration}})
                                <i :class="props.option.isPaid ? 'fa fa-dollar text-success ' : 'fa fa-dollar text-muted'"></i></span>

                        </div>
                    </template>
<span slot="noResult">{{ $t('No_Record_Found') }}</span>
                </multiselect>
<span class="val_msg">{{ singleBreakError? $t('BreakRequired') : '' }}</span>

            </div>
<div class="start-break col-12 float-left p-0" v-on:click="submitForm">
                <a class="btn btn-primary text-white btn-lg starttimer w-100" id="btnBreakStart" title="" style="cursor: pointer" data-toggle-tooltip="tooltip" type="submit">
                    <i class="fa fa-coffee pr-2"></i><span>{{$t('STARTBREAK')}}</span>
                </a>

            </div>
        </div>
    </div>
<div v-else>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3 p-0">
            <div class="heading-border">
                <h5 class="h5"><span id="SProjectDetailTitleSpan">{{$t('ManualBreak')}}</span></h5>
            </div>
        </div>
<ValidationObserver ref="form" v-slot="{ handleSubmit }">
            <form v-on:submit.prevent="handleSubmit(breakSubmit)" ref="form">
                <!-- <button type="button" class="close" style="background: none;color: #fff !important;" @click="$emit('close',{breakTimer:false,modalClose:false})"><em aria-hidden="true" class="fa fa-times"></em></button> -->
                <label>{{$t('selectBreak') }}<span class="text-danger">*</span></label>
                <div class="w-100 d-flex dynamic-multiselect" id="divModalTimetrack">

                    <multiselect @input="breakSelect" :placeholder="$t('selectBreak')" :multiple="true" class="multiselect p-0 w-100" v-model="selectBreak" :options="store.getBreakTimeList" label="name" track-by="value" :show-labels="false" id="exampleFormControlSelect1">
                        <template slot="option" slot-scope="props">
                            <div class="option__desc"><span class="option__title">{{
                                                props.option.name }} ({{ props.option.breakDuration}})
                                    <i :class="props.option.isPaid ? 'fa fa-dollar text-success ' : 'fa fa-dollar text-muted'"></i>
                                </span>

                            </div>
                        </template>
                        <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                    </multiselect>
                    <span class="val_msg">{{ breakError? $t('BreakRequired') : '' }}</span>

                </div>

                <div class="col-12 p-0 float-left ">
                    <div class="form-group newtimertracked"><label>{{$t('SelectDate')}}<span class="text-danger">*</span>:</label>
                        <div class="mb-3">
                            <DatePicker class="d-flex" v-model="breakData.BreakDate" :popover="false" @input="dateSelected" :maska="dateMaska">
                                <template #default="{ togglePopover, inputValue, inputEvents }">
                                    <input :placeholder="dateFormat" class="form-control datepicker_HS" :value="inputValue" v-on="inputEvents" @input="dateSelected" />
                                    <div class="input-group-append cursor-pointer" v-on:click="togglePopover">
                                        <span class="input-group-text">
                                            <em class="fa fa-calendar"></em>
                                        </span>
                                    </div>

                                </template>
                            </DatePicker>
                            <span class="val_msg">{{ dateError? $t('ExpDateRequired') : '' }}</span>
                        </div>

                    </div>
                </div>

                <!-- <div class="col-12 p-0" id="divModalTimetrack">

                    <ValidationProvider :rules="'required'" v-slot="{ errors }">
                        <input type="date" v-model="breakData.BreakDate" class="border-0 font-14" />
                        <span class="val_msg">{{ errors[0] ? $t('ExpDateRequired') : '' }}</span>
                    </ValidationProvider>

                </div> -->

                <div class="row flex-row-reverse">

                    <div class="text-end text-right col-12">
                        <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</button>
                        <button @click="$emit('close')" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
                    </div>

                </div>
            </form>
        </ValidationObserver>
    </div>
<loader :is-visible="isLoading"></loader>
</div>
</template>

<script>
import {
    defineComponent,
    onMounted,
    reactive,
    ref,
    onServerPrefetch
} from '@vue/composition-api'
import Multiselect from 'vue-multiselect'
import {
    userTimerInfo
} from "../../../stores/TimeTrack";
import {
    storeToRefs
} from "pinia";
import moment from "moment"
import {
    timeToSeconds,
    getSecondsFromDate,
    dateAndTimeZone,
    dateTimeFromat,
    dateFormat,
} from "@/assets/common/vue-common.js"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
extend('required', {
    validate(value) {
        console.log("check", value)
        return {
            required: true,
            valid: ['', null, value.length > 0].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    computesRequired: true
});
export default defineComponent({
    name: "Break Timer",
    components: {
        Multiselect,
        ValidationProvider,
        ValidationObserver,
        DatePicker
    },
    props: {
        userId: {
            type: Number,
            default: "my title",
        },
        timerModule: {
            type: Boolean
        },
        trackDataPouse: {
            type: Object
        },
        automaticBreak: {
            type: Boolean
        }
    },
    data() {
        return {
            dateMask: {
                input: dateFormat,
            },
            singleBreakError: false,
            buttons: false,
            //error: '',
            breakData: {
                BreakDate: dateAndTimeZone(moment(), dateTimeFromat),
                BreakId: [],
                trackAgent: "Web_Manual",
                CurrentDate:dateAndTimeZone(moment(), dateTimeFromat),
            },
            breakError: false,
            multiBreakError: false,
            dateError: false,
            selectBreak: [],
            singleBreakSelect: '',
            automaticShowBreak: this.automaticBreak,
            trackDataProps: this.trackDataPouse,
            isLoading: false
            // trackDataProps:this.trackDataPouse

        }
    },
    setup(props, context) {
        console.log("check", context.attrs)
        //if true open components from header timer else open from add timetrack module
        const breakModule = context.attrs.timerModule
        // const trackDataProps = reactive(context.attrs.trackDataPouse)
        const store = userTimerInfo();
        const formResponse = ref()
        const duration = ref()
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: false,
            format: false,
            date: null,
            trackId: 0,
            startTime: dateAndTimeZone(moment(), dateTimeFromat),
            stopTime: dateAndTimeZone(moment(), dateTimeFromat),
            companyId: null,
            currentDate: dateAndTimeZone(moment(), dateTimeFromat),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: context.attrs.userId,
            linkedTrackId: null,
            TotalHours: 0,
            timeformat: "12",
            TagIds: null,
            isForcedUpdate: null,
            type: "Break",
            BreakDate: dateAndTimeZone(moment(), dateTimeFromat),
            BreakName: null,
            BreakId: 0,
            BreakStartTime: dateAndTimeZone(moment(), dateTimeFromat),
            BreakStopTime: dateAndTimeZone(moment(), dateTimeFromat),
            Shift_ID: 0,
            SelectedDate: dateAndTimeZone(moment(), dateTimeFromat),
            Break_Shift: ""
        })

        const {
            getBreakTimeList,
            getTimerRunResponse
        } = storeToRefs(store)
        const breakTimer = reactive({
            breakTime: ''
        })
        const state = ref({
            submitted: false,
            error: ''
        })
        onMounted(() => {

        })

        return {
            breakTimer,
            state,
            getBreakTimeList,
            trackerData,
            getTimerRunResponse,
            formResponse,
            store,
            duration,
            breakModule,
            //trackDataProps

        }
    },

    created: function () {
        // console.log("trackDataPouse", this.trackDataPouse)
    },
    methods: {
        changeBreakModal() {
            this.automaticShowBreak = this.automaticShowBreak = !this.automaticShowBreak
        },
        submitForm: async function () {
            this.singleBreak();
            if (this.singleBreakSelect) {
                this.isLoading = true
                // const time = this.store.getBreakTimeList.find((data) => data.name == this.FormSchema[0].Data[0].value.label)
                this.trackerData.BreakId = this.singleBreakSelect.value
                this.trackerData.BreakName = this.singleBreakSelect.name
                this.trackerData.Shift_ID = this.store.timerInfo.shift_ID
                this.trackerData.trackDescription = this.singleBreakSelect.name
                this.trackerData.isBillable = this.singleBreakSelect.isPaid
                await this.store.getBreaksDetails(`BreakId=${this.trackerData.BreakId}&BreakDate=${dateAndTimeZone(moment(), dateTimeFromat)}`)
                if (this.store.getbreakDetails.statusCode == 0) {
                    if (this.store.timerInfo.statusCode == "ACT") {
                        this.trackDataProps.stopTime = dateAndTimeZone(moment(), dateTimeFromat)
                        this.trackDataProps.totalHours = this.store.$state.timerCountInSec
                        this.trackDataProps.timerStatus = 'pause'
                        this.trackDataProps.trackId = this.store.timerInfo.trackId
                        this.trackDataProps.createdBy = this.trackerData.CreatedBy
                        this.trackDataProps.projectId = typeof this.trackDataProps.projectId == "object" && this.trackDataProps.projectId ? this.trackDataProps.projectId.projectId : this.trackDataProps.projectId
                        this.trackDataProps.taskId = typeof this.trackDataProps.taskId == "object" && this.trackDataProps.taskId ? this.trackDataProps.taskId.value : this.trackDataProps.taskId
                        this.trackDataProps.trackDescription = this.store.timerInfo.trackDescription
                        await this.store.submitProjectInfo(this.trackDataProps)
                        if(this.store.getTimerRunResponse.status==0){
                        this.store.stopTimer()
                        }
                    }
                    this.trackerData.BreakStartTime = dateAndTimeZone(moment(), dateTimeFromat)
                    await this.store.submitProjectInfo(this.trackerData);
                    if(this.store.getTimerRunResponse.status==0){
                    await this.store.getTimerInfo();
                    // const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    //this.store.getTimerListing1(url)
                    if (this.store.timerInfo.trackType == 'Break') {

                        let seconds = getSecondsFromDate({
                            startDate: this.store.getTimer?.dateStart,
                            endDate: dateAndTimeZone(moment(), dateTimeFromat)
                        }, 'seconds')
                        this.store.$state.breakTimerCountInSec = timeToSeconds(this.store.timerInfo.remainingBreakDuration) - seconds
                        this.store.$state.totalBreakRemainTime = this.store.$state.breakTimerCountInSec > 0 ? this.store.$state.breakTimerCountInSec : 0
                        this.store.breakStartTimer()

                        //breakTimer for timer and breack show in header
                        this.$emit("close", {
                            "breakTimer": true,
                            "modalClose": false
                        })
                    } else {
                        // this.error = this.store.getTimerRunResponse?.userMsg;
                    }
                }

                } else {
                    this.ShowAlert(this.store.getbreakDetails.mSg, "danger", true, this.$t('Alert'));
                }
                this.isLoading = false
            }

        },
        async breakSubmit() {
            if (this.selectBreak.length == 0 && this.breakData.BreakDate != null) {

                this.breakError = this.selectBreak.length == 0 ? true : false
                this.dateError = this.breakData.BreakDate === null ? true : false
            } else {
                this.breakError = this.selectBreak.length == 0 ? true : false
                this.dateError = this.breakData.BreakDate === null ? true : false
            }
            if (!this.breakError && !this.dateError) {
                this.isLoading = true
                let startDateTime = moment(moment(this.breakData.BreakDate).format(dateFormat))
                let today = moment(dateAndTimeZone(moment(), dateFormat))
                if (startDateTime.valueOf() > today.valueOf()) {
                    // this.store.alertBOxForMessage({
                    //     "type": 'warning',
                    //     "open": true,
                    //     "message": "You can not enter time for the future. Please update the time entered"
                    // })
                    this.ShowAlert(this.$t('FutureTimenotAllowed'), "warning", true, this.$t('Alert'));

                } else {
                    this.breakData.BreakId = []
                    this.selectBreak.map((item) => {
                        this.breakData.BreakId.push(item.value)
                    })
                    this.breakData.BreakDate = moment(this.breakData.BreakDate).format("YYYY-MM-DD HH:mm:ss")
                    this.breakData.CurrentDate=dateAndTimeZone(moment(), "YYYY-MM-DD HH:mm:ss"  )
                    //await this.store.getBreaksDetails(`BreakId=${this.breakData.BreakId}&BreakDate=${this.breakData.BreakDate}`)
                    //if (this.store.getbreakDetails.statusCode == 0) {
                    await this.store.addManualBreak(this.breakData);
                    if (this.store.getAddManualBreakResponse.status == 0) {
                        this.$emit("close", {
                            "breakTimer": true,
                            "modalClose": false
                        })
                        const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                        this.store.getTimerListing1(url)
                        const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                        this.store.getTrackedtimings(url1)
                        this.breakModalPopup = false
                        this.selectBreak = []
                        this.breakData.BreakId = []

                    } else if (this.store.getAddManualBreakResponse.status == 1 && (this.store.getAddManualBreakResponse.userMsg == 'OVERTIME' ||this.store.getAddManualBreakResponse.userMsg == '102')) {

                        this.ShowAlert(this.$t('OverTimeNotAllowed'), "warning", true, this.$t('Alert'));
                    } else if (this.store.getAddManualBreakResponse.status == 1 && this.store.getAddManualBreakResponse.userMsg == 'Holiday') {
                        this.ShowAlert(this.$t('AllowTrackingTimeOnHoliday_desc'), "warning", true, this.$t('Alert'));
                    } else if (this.store.getAddManualBreakResponse.status == 1 && this.store.getAddManualBreakResponse.userMsg == "Future entry not allowed") {
                        this.ShowAlert(this.$t('FutureTime'), "warning", true, this.$t('Alert'));

                    } else {
                        this.ShowAlert(this.$t(this.store.getAddManualBreakResponse.userMsg), "danger", true, this.$t('Alert'));
                    }
                    // } else {
                    //     this.ShowAlert(this.store.getbreakDetails.mSg, "danger", true, this.$t('Alert'));
                    // }
                }
                this.isLoading = false
            }

        },

        dateSelected() {
            
            if (this.breakData.BreakDate) {
                this.dateError = false
            } else {
                this.dateError = true
            }
        },

        breakSelect() {
           
            if (this.selectBreak.length > 0) {
                this.breakError = false
            } else {
                this.breakError = true
            }
        },
        singleBreak() {

            if (this.singleBreakSelect) {
                this.singleBreakError = false
            } else {
                this.singleBreakError = true
            }

        }

    }

})
</script>
